.paginator {
  width: 34px;
  text-align: center;
  cursor: pointer;
}

.wui-breadcrumb {
  height: 30px;
  line-height: 30px;
  margin: 0;
  padding: 0;
  padding-left: 10px;
}

.wui-breadcrumb li {
  list-style: none;
  float: left;
  font-size: 16px;
  margin-right: 5px;
}

.com { color: #93a1a1; }
.lit { color: #195f91; }
.pun, .opn, .clo { color: #93a1a1; }
.fun { color: #dc322f; }
.str, .atv { color: #D14; }
.kwd, .linenums .tag { color: #1e347b; }
.typ, .atn, .dec, .var { color: teal; }
.pln { color: #48484c; }

.prettyprint {
  padding: 8px;
  background-color: #f7f7f9;
  border: 1px solid #e1e1e8;
}
.prettyprint.linenums {
  -webkit-box-shadow: inset 40px 0 0 #fbfbfc, inset 41px 0 0 #ececf0;
  -moz-box-shadow: inset 40px 0 0 #fbfbfc, inset 41px 0 0 #ececf0;
  box-shadow: inset 40px 0 0 #fbfbfc, inset 41px 0 0 #ececf0;
}

/* Specify class=linenums on a pre to get line numbering */
ol.linenums {
  margin: 0 0 0 33px; /* IE indents via margin-left */
}
ol.linenums li {
  padding-left: 12px;
  color: #bebec5;
  line-height: 18px;
  text-shadow: 0 1px 0 #fff;
}

.content {
  min-width: 970px;
}

.table>tbody>tr>td {
  vertical-align: middle;
}

.head-link {
  family: MicrosoftYaHei;
  font-size: 16px;
  line-height: 50px;
  color: #ffffff;
  margin-left: 20px;
}

.head-active {
  family: MicrosoftYaHei;
  font-size: 16px;
  line-height: 50px;
  color: #94ccee;;
  margin-left: 20px;
}


